import { useStaticQuery, Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import JeremyImg from "../images/jeremy/rest.jpg"
import JeremyCool from "../images/jeremy/cool.jpg"
import PageWidth from "./pagewidth"
import Img from "gatsby-image"
import "./about.css"

const About = ({ siteTitle }) => {

  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "jeremy/KakaoTalk_20190712_235427974_02.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <div>
      <section className="section-1">
      	<div className="section-1-img-wrapper">
          <Img className="section-1-img" fluid={data.placeholderImage.childImageSharp.fluid} />
        </div>
      	<article className="section-1-text">
          <p>Life summary:</p>
          <ul>
            <li>Born in Alexandria, VA.</li>
            <li>Taught myself C++ at 12 years old because I wanted to make video games</li>
            <li>Graduated from Virginia Tech with a B.S. in math</li>
            <li>Graduated from Columbia University with a M.S. in operations research</li>
            <li>Worked at a bunch of cool companies in NYC</li>
            <li>Took a year off to travel the world</li>
            <li>Working at cool companies again, except this time remotely</li>
          </ul>
          <p>Random Facts:</p>
          <ul>
            <li>I've been to <Link to="/places-ive-been">33+ countries</Link></li>
            <li>I was an <Link to="/vroom">Engineering Lead for a company with $1b+ in revenue</Link>, building out their flagship ecommerce website from scratch on a modern tech stack <a href="https://www.linkedin.com/in/jeremysbernier">(LinkedIn)</a></li>
            <li>I majored in math</li>
            <li>I'm fluent in Japanese and spent my summers in the countryside of Japan as a kid going to school there. It was totally different from the U.S in every single way imaginable, and had a strong impact on me.</li>
            <li>Other interests/hobbies: economics / politics, video making, electronic music production, piano, game development, self-development</li>
          </ul>
          <p>Actually there's a lot more to talk about, but since this website simply serves to help get me hired, we'll leave it at that for now until we become close enough friends to where we can discuss the deeper things in life.</p>
      	</article>
      </section>
  {/*    <section className="section-1">
        <div>About stuff goes here</div>
        <div>
          <img style={{maxWidth: '600px'}} src={JeremyKoreaConfident} />
        </div>
      </section>*/}
    </div>
)}

About.propTypes = {
}

About.defaultProps = {
}

export default About
